.popup__container {
    width: 430px;
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    position: relative;
}


@media screen and (max-width: 566px) {
    .popup__container {
        width: 282px;
    }
}