.popup__field {
    margin: 45px 36px 0px;
    border: 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    font-family: 'Inter', Arial, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    padding: 0;
    padding-bottom: 12px;
    outline: 0;
}

.popup__field:last-of-type {
    margin: 0 36px 0;
}


@media screen and (max-width: 566px) {
    .popup__field {
        margin: 75px 22px 0px;
    }

    .popup__field:last-of-type {
        margin: 0 22px 0;
    }
}
